// src/components/ventas/VentaForm.tsx
'use client';
import React, { useState } from 'react';
import { VentaServicio, Cliente, DetalleServicio } from '../../entities/venta';
import { useVentas } from '../../hooks/useVentas';
import { formatCurrency } from '../../lib/logistica/formatters';

const clientesDummy: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'Consultora ABC', telefono: '999999999', correo: 'abc@mail.com' },
  { idCliente: 'CL-002', nombre: 'Hotel Pacifico', telefono: '988888888', correo: 'hotel@pacifico.pe' }
];

const serviciosDisponibles = [
  { idServicio: 'SVC-001', nombre: 'Instalación Red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte Técnico', costo: 100 }
];

const VentaForm = () => {
  const { registrarVenta } = useVentas();
  const [clienteSeleccionado, setClienteSeleccionado] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);

  const agregarDetalle = (servicioId: string) => {
    if (detalles.find(d => d.idServicio === servicioId)) return;
    const servicio = serviciosDisponibles.find(s => s.idServicio === servicioId);
    if (servicio) {
      const nuevoDetalle: DetalleServicio = {
        idDetalleServicio: `DET-${Date.now()}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo
      };
      setDetalles(prev => [...prev, nuevoDetalle]);
    }
  };

  const costoTotal = detalles.reduce((acc, s) => acc + s.costoTotal, 0) - descuento;

  const handleSubmit = () => {
    if (!clienteSeleccionado || detalles.length === 0) return;
    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente: clienteSeleccionado,
      detalles,
      descuento,
      costoTotal,
      fechaRegistro: new Date().toISOString()
    };
    registrarVenta(venta);
    alert('Venta registrada con éxito');
    setDetalles([]);
    setClienteSeleccionado(null);
    setDescuento(0);
  };

  return (
    <div className="space-y-4">
      <select
        className="border p-2 rounded w-full"
        value={clienteSeleccionado?.idCliente || ''}
        onChange={(e) => {
          const cliente = clientesDummy.find(c => c.idCliente === e.target.value);
          if (cliente) setClienteSeleccionado(cliente);
        }}
      >
        <option value="">Seleccionar cliente</option>
        {clientesDummy.map(c => (
          <option key={c.idCliente} value={c.idCliente}>{c.nombre}</option>
        ))}
      </select>

      <select
        className="border p-2 rounded w-full"
        onChange={(e) => {
          if (e.target.value !== '') agregarDetalle(e.target.value);
          e.target.value = '';
        }}
      >
        <option value="">Agregar servicio</option>
        {serviciosDisponibles.map(s => (
          <option key={s.idServicio} value={s.idServicio}>
            {s.nombre} - {formatCurrency(s.costo)}
          </option>
        ))}
      </select>

      <ul className="list-disc pl-5 text-sm text-gray-700">
        {detalles.map(d => (
          <li key={d.idDetalleServicio}>{d.nombreServicio} - {formatCurrency(d.costoTotal)}</li>
        ))}
      </ul>

      <input
        type="number"
        placeholder="Descuento (S/)"
        className="border p-2 rounded w-full"
        value={descuento}
        onChange={(e) => setDescuento(Number(e.target.value))}
      />

      <p className="text-sm text-gray-700">Total: <strong>{formatCurrency(costoTotal)}</strong></p>

      <button
        onClick={handleSubmit}
        disabled={!clienteSeleccionado || detalles.length === 0}
        className="bg-blue-600 text-white px-4 py-2 rounded disabled:bg-gray-400"
      >
        Registrar Venta
      </button>
    </div>
  );
};

export default VentaForm;
